as, el manejo apropiado y oportuno de las quejas y reclamos les representa el mejoramiento de la calidad del servicio y del producto. Es un tema relevante que tiene un proceso y un mecanismo de respuesta. ")]),_c('p',{staticClass:"mb-5",attrs:{"data-aos":"fade-down-left"}},[_vm._v("Los promotores de ventas deben estar preparados para reconocer las acciones más simples que puede tener un cliente por motivo de queja, esto incluye el conocimiento sobre normatividad de protección al consumidor y sus entidades de control. Estas hacen valer sus derechos; de no estar atentos a sus requerimientos y brindar las soluciones pertinentes, puede traer consecuencias como: restricción de la venta del producto, exigencia del cambio de publicidad y acciones legales, entre otras. De otro lado, el promotor también debe saber que a las empresas les interesa tener un excelente servicio al cliente con el fin de mantenerlo satisfecho y evitar futuras quejas, porque finalmente repercutirá en sus ventas.")]),_vm._m(16),_vm._m(17),_c('Separador'),_c('p',{staticClass:"mb-5",attrs:{"data-aos":"fade-down-right"}},[_vm._v("Las quejas pueden presentarse para productos, bienes o servicios. En el caso del producto las quejas por calidad pueden ser:")]),_vm._m(18),_c('p',{staticClass:"mb-5",attrs:{"data-aos":"fade-down-right"}},[_vm._v("Los clientes buscan una compensación a raíz de su queja o reclamo porque sienten que la empresa les falló en alguna manera. El tipo de solicitudes que se pueden tener por parte del cliente son:")]),_c('div',{staticClass:"row justify-content-center mb-5",attrs:{"data-aos":"flip-up"}},[_c('div',{staticClass:"col-xxl-3 col-lg-4 col-sm-10 col-md-8 mb-xxl-0 mb-4",attrs:{"data-aos":"fade-right"}},[_c('div',{staticClass:"tarjetas-upper",on:{"mouseover":function($event){_vm.mostrarIndicador = false}}},[(_vm.mostrarIndicador)?_c('div',{staticClass:"indicador--hover"}):_vm._e(),_c('img',{attrs:{"src":require("@/assets/curso/temas/tema7/img-018.png"),"alt":"Imagen decorativa"}})