 etapa en el proceso de servicio al cliente son:")]),_vm._m(23),_c('p',{attrs:{"data-aos":"fade-down-right"}},[_vm._v("Cualquier variación de este proceso entre cliente y vendedor genera una queja o un reclamo, ya sea con causa justificada, o que el cliente percibió de otra manera lo que quería o deseaba y el producto no cumplió sus expectativas porque en ese momento la necesidad del cliente es otra; hay que detectarla y satisfacerla dándole solución, la técnica más adecuada sería entonces la “preparación a través de las preguntas”, sin olvidar el paso inicial que fue “escuchar” hasta lograr entender la queja del cliente y asegurar la solución, que incluso, podría ser una nueva venta y en ese caso se repetirían los pasos 4 al 7.")]),_c('p',{staticClass:"mb-5",attrs:{"data-aos":"fade-down-left"}},[_vm._v("En las teorías de servicio al cliente se conocen unas reglas generales para el manejo de quejas y reclamos:")]),_vm._m(24),_c('Separador'),_vm._m(25),_c('p',{staticClass:"mb-5",attrs:{"data-aos":"fade-down-right"}},[_vm._v("Se describe la comunicación como el proceso de transmitir y recibir mensajes, ésta tiene unos elementos mínimos: el emisor, el mensaje y el receptor. En el proceso de servicio al cliente sigue siendo muy importante el tema de la comunicación, considerando que es el medio a través del cual se reciben y procesan las quejas y reclamos. Es necesario tener en cuenta que la comunicación no sólo se hace a través de palabras o en forma escrita, también las señales pueden dar ideas incluso antes de pronunciar la primera palabra, esto ha sido denominado comunicación no verbal, y se incluye en la categoría de comunicación oral, dado que puede darse a través de sonrisas, un gesto o sonido, entre otras. Para tener una buena comunicación deben reconocerse todas las señales disponibles. ")]),_vm._m(26),_vm._m(27),_vm._m(28),_c('p',{staticClass:"mb-5",attrs:{"data-aos":"fade-down-right"}},[_vm._v("Aunque la recomendación sobre tratar de escuchar atentamente y no interrumpir al cliente o a quien hace el reclamo, no significa que no se pueda ayudar al cliente con preguntas en el momento oportuno que permitan identificar su necesidad. Katz (1991) determina que existen 5 tipos de preguntas:")]),_c('div',{staticClass:"col-xl-10 mx-auto mb-5"},[_c('div',{staticClass:"d-flex flex-wrap align-items-center mb-5"},[_c('div',{staticClass:"col-xl-7 order-2 order-xl-1 pe-xl-4"},[_c('LineaTiempoD',{staticClass:"color-primario linea-tiempo-edit",attrs:{"data-aos":"fade-right"}},[_c('div',{staticClass:"d-block",attrs:{"numero":"1","titulo":"Neutral cerrada"}},[_c('p',{staticClass:"mb-2"},[_vm._v("Genera la respuesta monosilábica. Si o no. Puede dar información pero, no estimula a seguir conversando. ")])]),_c('div',{staticClass:"d-block",attrs:{"numero":"2","titulo":"Neutral abierta"}},[_c('p',{staticClass:"mb-2"},[_vm._v("La pregunta de este tipo usualmente comienzan con ¿Qué?, ¿Por qué? o ¿Cómo? e invita a la persona interrogada a hablar del tema en términos de sus propias prioridades.")])]),_c('div',{staticClass:"d-block",attrs:{"numero":"3","titulo":"Neutral dirigida"}},[_c('p',{staticClass:"mb-2"},[_vm._v("Las preguntas neutrales dirigidas suelen comenzar con las palabras ¿Cuándo? ¿Dónde? o ¿Quién? Por tanto, exigen una respuesta más directa o precisa.")])]),_c('div',{staticClass:"d-block",attrs:{"numero":"4","titulo":"Cargada con signo negativo"}},[_c('p',{staticClass:"mb-2"},[_vm._v("Esta pregunta define en gran medida la respuesta. La preguntas cargadas con signo negativo suele comenzar con “usted no hace……”, “usted no….”")])]),_c('div',{staticClass:"d-block",attrs:{"numero":"5","titulo":"Cargada con signo positivo"}},[_c('p',{staticClass:"mb-2"},[_vm._v("Hacen cumplidos exagerados con el propósito de guiar la respuesta por un camino favorable.")])])])],1),_c('img',{staticClass:"col-lg-5 col-8 col-md-6 order-lg-2 order-1 mb-lg-0 mb-4",staticStyle:{"max-width":"610px"},attrs:{"src":require("@/assets/curso/temas/tema7/img-024.svg"),"